import React from "react";
import s from "./home.module.scss";
import { NavLink } from "react-router-dom";

const Home = () => {
    return(
        <div className={s.wrapper}>
            <h1 class={s.title}>!</h1>
            <p class={s.subtitle} translate="no">NASA Everyday -  ,        '       ,  -.          ,    .</p>
            <div class={s.nav}>
                <NavLink to="./application">
                    <button class={s.button__start}>!</button>
                </NavLink>
                <NavLink to="./docs">
                    <button class={s.button__docs}>   ?</button>
                </NavLink>
            </div>
            
        </div>  
    );
};

export default Home;